import React, {useEffect, useState} from 'react'

import {Skeleton, Row, Col, Select, Spin, Pagination, DatePicker} from 'antd'

import {Link, withRouter} from 'react-router-dom'

import {KTSVG} from '../../../_metronic/helpers'
import moment from 'moment'
import 'react-datepicker/dist/react-datepicker.css'
import ShowMore from 'react-show-more'
import {mainConfig} from '../../../config/mainConfig'

const {Option} = Select

let arrIndex = 0,
  token = '',
  count = 1,
  sortflag = 0
const {RangePicker} = DatePicker

function getWindowDimensions() {
  const {innerWidth: width, innerHeight: height} = window
  return {
    width,
    height,
  }
}

const AllBackground=(props: any)=> {
  const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions())

  const [loading, setloading] = useState<any>(false)
  const [loadingGrn, setloadingGrn] = useState<any>(false)
  const [ProductId, setProductId] = useState<any>('')

  const [historical_comments, sethistorical_comments] = useState<any[]>([])

  const [prodHeader, setProdHeader] = useState<any>({})
  const [supplierTableData, setSupplierTableData] = useState<any[]>([])
  const [nameState, setNameState] = useState(props)
  const [row_count, setrow_count] = useState<any>(1)
  const [current, setCurrent] = useState(1)
  const [contactInfo, setContactInfo] = useState<any>({
    keyword: '',
    user: '',
  })
  const [fromDate, setfromDate] = useState<any>(null)
  const [toDate, settoDate] = useState<any>(null)

  const [sordAdded, setsordAdded] = useState<any>('Desc')
  const [loadingComments, setloadingComments] = useState<any>(false)
  const [username, setusername] = useState<any>(null)
  const [users, setUsers] = useState<any[]>([])
  const [paginflag, setpaginflag] = useState<any>(true)

  useEffect(() => {
    if (props.location.state != undefined) {
      localStorage.setItem('productId', props.location.state.data)
    }
    setNameState(props)

    let prodData =
      nameState.location.state != undefined
        ? nameState.location.state.data
        : localStorage.getItem('productId')
    setProductId(prodData)
    getData(prodData, count, sortflag)
  }, [props])

  function getData(data: any, count: any, sortflag: any) {
    setloadingComments(true)

    let n = 1

    try {
      fetch(
        `${mainConfig.MAIN_ENDPOINT}/api/get-historical-background/${
          data != undefined || '' ? data : localStorage.getItem('productId')
        }/${count}/${sortflag}`,
        {
          method: 'get',
          headers: {
            Authorization: `Bearer ${token}`,
            Accept: 'application/json',
            'Content-Type': 'application/json',
          },
        }
      )
        .then(function (response) {
          if (response.status != 200) {
            localStorage.clear()
            window.location.reload()
          }

          response.json().then(function (data) {
            setloadingComments(false)

            setSupplierTableData(data.data.historical_back)
            setrow_count(data.data.row_count)
            setUsers(data.data.users)

            sethistorical_comments(data.data.historical_back)

            setProdHeader(data.data.product_details)

            setloading(false)
            setloadingGrn(false)
            setpaginflag(true)
          })
        })
        .catch(function (err) {
          setloading(false)
        })
    } catch (err) {
      console.log('ERR>>>', err)
    }
  }

  const handleTableChange = (page: any) => {
    let total = row_count

    setCurrent(page)

    let prodData =
      nameState.location.state != undefined
        ? nameState.location.state.data
        : localStorage.getItem('productId')

    getData(prodData, page, sortflag)
  }
  const handleSubmit = (e: any) => {
    
    setContactInfo({
      keyword: '',
      user: '',
    })
    setusername(null)
    setfromDate(null)
    settoDate(null)

    e.preventDefault()
    let prodData1 = {
      productid: ProductId,
      from_date: fromDate,
      to_date: toDate,
      comment: contactInfo.keyword,
      added_by: username,
    }

    try {
      fetch(`${mainConfig.MAIN_ENDPOINT}/api/search-historical-background`, {
        method: 'post',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(prodData1),
      })
        .then(function (response) {
          if (response.status != 200) {
            return
          }

          response.json().then(function (data) {
            setloading(false)

            sethistorical_comments(data.data.historical_background)

            setloading(false)
            setloadingGrn(false)
            setpaginflag(false)
          })
        })
        .catch(function (err) {
          setloading(false)
        })
    } catch (err) {
      console.log('ERR>>>', err)
    }
  }

  const ondateSelect = (date: any) => {
    if (date) {
      let tempDate = moment(date[0]).format('YYYY-MM-DD')
      let tempDate1 = moment(date[1]).format('YYYY-MM-DD')
      setfromDate(tempDate)
      setfromDate(tempDate)

      settoDate(tempDate1)
    } else {
      getData(ProductId, 1, sortflag)
    }
  }
  const reset = () => {
    getData(ProductId, 1, sortflag)
    setpaginflag(false)
    setusername(null)
  }

  const onSortAddedon = (data: any) => {
    if (data == 0) {
      sortflag++

      setsordAdded('Asc')
      getData(ProductId, current, sortflag)
    } else {
      sortflag--

      setsordAdded('Desc')
      getData(ProductId, current, sortflag)
    }
  }

  token = JSON.parse(localStorage.getItem('portalUserDataToken') || '')

  return (
    <>
      {loading == true ? (
        <React.Fragment>
          <Skeleton loading={loading} active></Skeleton>
          <Skeleton loading={loading} active></Skeleton>
          <Skeleton loading={loading} active></Skeleton>
        </React.Fragment>
      ) : (
        <React.Fragment>
          <Row style={{marginTop: '4%'}}>
            <Col span={20}>
              <ol className='breadcrumb breadcrumb-dot fs-6 fw-bold' style={{margin: '1%'}}>
                <Link to='/home' className='pe-1 cursor-pointer'>
                  <li className='breadcrumb-item pe-1'>Home</li>
                </Link>
                / &nbsp;
                <Link to={{pathname: '/product', state: {prod_id: ProductId, data: prodHeader}}}>
                  <li className='breadcrumb-item pe-1'>Product</li>
                </Link>
                /&nbsp;<li className='breadcrumb-item pe-3'>All Background</li>
              </ol>
            </Col>

            <Col>
              <Link to={{pathname: '/product', state: {prod_id: ProductId, data: prodHeader}}}>
                <button className='btn btn-sm btn-primary '>Back To Product</button>
              </Link>
            </Col>
          </Row>
          {windowDimensions.width > 800 ? (
            <React.Fragment>
              <div className='toolbar' id='kt_toolbar'>
                <div id='kt_toolbar_container' className='container-fluid d-flex flex-stack'>
                  <div className='d-flex align-items-center py-5 justify-content-space-between '>
                    <a className='btn btn-outline btn-outline-dashed m-1' style={{cursor: 'text'}}>
                      <span
                        className='card-label fs-2 mb-1 text-purple header-custom'
                        style={{color: 'rgb(138, 38, 80)'}}
                      >
                        {prodHeader.parent_product_code}{' '}
                        <span className='text-muted mt-1 fw-bold fs-8 header-custom-2 '>
                          Aggregate Code
                        </span>
                      </span>
                    </a>
                  </div>
                  {prodHeader.clean_description == '' ? (
                    <div className='d-flex align-items-center py-5 justify-content-space-between'></div>
                  ) : (
                    <div className='d-flex align-items-center py-5 justify-content-space-between'>
                      <a
                        className='btn btn-outline btn-outline-dashed m-1'
                        style={{cursor: 'text'}}
                      >
                        {' '}
                        <span
                          className='card-label fs-2 mb-1 text-purple header-custom'
                          style={{color: 'rgb(138, 38, 80)'}}
                        >
                          {prodHeader.clean_description}
                        </span>
                      </a>
                    </div>
                  )}

                  <div className='d-flex align-items-center py-5 justify-content-space-between'></div>
                </div>
              </div>
            </React.Fragment>
          ) : null}

          <div className='row gy-5 g-xl-10' style={{marginTop: '20px'}}></div>

          {loadingComments == true ? (
            <Spin tip='Loading...' size='large' style={{justifyContent: 'center'}} />
          ) : (
            <div className='row g-5 gx-xl-8'>
              <div className='col-xl-12'>
                <div className='card-xxl-stretch mb-4 mb-xxl-7'>
                  <div className='row gy-5 g-xl-4'>
                    <div className={`card`}>
                      <div className='card-header border-0 pt-2'>
                        <h3 className='card-title align-items-start flex-column'>
                          <span
                            className='card-label fw-bolder fs-3 mb-1'
                            style={{color: 'rgb(138, 38, 80)'}}
                          >
                            All Backgrounds
                          </span>
                        </h3>
                        <form onSubmit={handleSubmit}>
                          <Row>
                            <div className='card-toolbar'>
                              <Col xs={{span: 5, offset: 1}} lg={{span: 7, offset: 1}}>
                                <RangePicker onChange={(date) => ondateSelect(date)} />
                              </Col>
                              <Col xs={{span: 5, offset: 1}} lg={{span: 4, offset: 1}}>
                                <input
                                  type='text'
                                  className='form-control form-control-solid'
                                  placeholder='Filter by keyword'
                                  name='keyword'
                                  value={contactInfo.keyword}
                                  onChange={(e) => {
                                    setContactInfo({
                                      ...contactInfo,
                                      [e.target.name]: e.target.value,
                                    })
                                  }}
                                />
                              </Col>
                              <Col xs={{span: 5, offset: 1}} lg={{span: 4, offset: 1}}>
                                <Select
                                  placeholder='Please Select User'
                                  value={username}
                                  onChange={(e) => {
                                    setusername(e)
                                  }}
                                >
                                  {users.map((a) => {
                                    return <Option value={a.id}>{a.username}</Option>
                                  })}
                                </Select>
                              </Col>
                              <Col xs={{span: 2, offset: 1}} lg={{span: 2, offset: 1}}>
                                <button className='btn btn-sm btn-primary ' type='submit'>
                                  Submit
                                </button>
                              </Col>
                              <Col xs={{span: 2, offset: 1}} lg={{span: 2}}>
                                <button
                                  className='btn btn-sm btn-danger '
                                  type='reset'
                                  onClick={reset}
                                >
                                  Reset
                                </button>
                              </Col>
                            </div>
                          </Row>
                        </form>
                      </div>

                      <div className='card-body py-3 table-align'>
                        <div className='table-responsive h-600px table-scroll'>
                          <table className='table table-row-dashed table-row-gray-300 align-middle gs-0 gy-4 main-table '>
                            <thead>
                              <tr className='fw-bolder text-muted'>
                                <th className='min-w-100px' style={{textAlign: 'left'}}>
                                  Comment
                                </th>
                                <th className='min-w-200px' style={{textAlign: 'left'}}>
                                  Added by
                                </th>

                                <th
                                  className='min-w-200px'
                                  style={{textAlign: 'left'}}
                                  onClick={() => onSortAddedon(sortflag)}
                                >
                                  Added on
                                  {sordAdded != 'Desc' ? (
                                    <KTSVG
                                      className='text-hover-primary cursor-pointer'
                                      path={'/media/icons/duotune/arrows/arr062.svg'}
                                    />
                                  ) : (
                                    <KTSVG
                                      className='text-hover-primary cursor-pointer'
                                      path={'/media/icons/duotune/arrows/arr068.svg'}
                                    />
                                  )}
                                </th>
                              </tr>
                            </thead>

                            <tbody>
                              {historical_comments != undefined &&
                                historical_comments.map((w) => {
                                  return (
                                    <tr>
                                      <td style={{maxWidth: '250px', textAlign: 'left'}}>
                                        <span className='text-gray text-hover-primary fw-bold fs-6 align-left'>
                                          <ShowMore
                                            lines={1}
                                            more='Show more'
                                            less='Show less'
                                            anchorClass=''
                                          >
                                            {w.comment}
                                          </ShowMore>
                                        </span>
                                      </td>

                                      <td style={{minWidth: '200px', textAlign: 'left'}}>
                                        <span className='text-dark fw-bolder text-hover-primary d-block fs-6 m-2'>
                                          {w.added_by}
                                        </span>
                                      </td>

                                      <td style={{minWidth: '200px', textAlign: 'left'}}>
                                        <span className='text-dark fw-bolder text-hover-primary d-block fs-6 m-2'>
                                          {moment(w.created_at).format('DD-MMM-YYYY')}
                                        </span>
                                      </td>
                                    </tr>
                                  )
                                })}
                            </tbody>
                          </table>
                        </div>
                        {paginflag == true ? (
                          <Pagination
                            defaultCurrent={1}
                            total={row_count}
                            onChange={handleTableChange}
                            current={current}
                            style={{margin: '2%'}}
                          />
                        ) : (
                          ''
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}
        </React.Fragment>
      )}
    </>
  )
}

export default withRouter(AllBackground)
